import React,  { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import './AnnotationIssues.css'
import Collapse from 'react-bootstrap/Collapse'
import { FaChevronDown } from 'react-icons/fa';



function handleMouseOver(props,key) {
    props.onMouseOver(key);
}

function handleMouseOut(props,key) {
    props.onMouseOut(key);
}

function handleChange(props, event) {
    console.log(props.issues);
    let x = event.target.name;
    let issue = {
        id: event.target.id,
        displayName: event.target.name,
        issue: event.target.value,
        open:false
    }
    if (props.issues.some(e => e.id === issue.id)) {
        /* vendors contains the element we're looking for */
        props.issues[props.issues.findIndex(x => x.id === issue.id)] = issue;
    }
    else {
        props.issues.push(issue);
    }
}

function handleSubmit() {

}


function AnnotationIssues(props) {
    const [open, setOpen] = useState(false);
    return (
        <div>

            {props.annotations.map(annotation => (
                <div
                    onMouseOver={props.onMouseOver(annotation.data.id)}
                    onMouseOut={props.onMouseOut(annotation.data.id)}
                    key={annotation.data.id}
                    className="annotationIssue col-12"
                >
                    <div
                        onClick={() => setOpen(!open)}
                        aria-controls={annotation.data.id}
                        aria-expanded={open}
                        className="issueHeader row"
                    >
                        <div class="col-10">
                        <h3>{annotation.data.text}</h3> 
                        </div>
                       
                       <h3 className="float-left"><FaChevronDown/></h3>
                       
      </div>
                    <Collapse in={open}>
                        <div id={annotation.data.id}>
                          
                    <h3></h3>
                    <form onChange={ e => handleChange(props, e)}>
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name={annotation.data.text} id={annotation.data.id} value="option1" />
                            <label class="form-check-label" for="exampleRadios1">
                                Default radio
                            </label>
                        </div>
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name={annotation.data.text} id={annotation.data.id} value="option2" />
                            <label class="form-check-label" for="exampleRadios2">
                                Second default radio
                            </label>
                        </div>
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name={annotation.data.text} id={annotation.data.id} value="option2" />
                            <label class="form-check-label" for="exampleRadios2">
                                Second default radio
                            </label>
                        </div>
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name={annotation.data.text} id={annotation.data.id} value="option2" />
                            <label class="form-check-label" for="exampleRadios2">
                                Second default radio
                            </label>
                        </div>
                        <input type="text" />
                    </form>
        </div>
                    </Collapse>

                </div>
            ))}
        </div>
    );


}

export default AnnotationIssues;